<template>
  <keep-alive>
    <div>
      <van-popup v-model="show" closeable position="left" :style="{ width: '40%' ,height:'100%'}" />
      <van-nav-bar fixed title="酷选音乐">
        <template #left>
          <van-icon name="bars" :size="30" @click="showPopup" />
        </template>
      </van-nav-bar>

      <van-tabs v-model="active" animated style="margin-top:46px" swipeable sticky>
        <van-tab title="推荐" fixed>
          <Tabb :pic-list="picList" />
          <van-panel title="热门歌单" desc status>
            <List :hot-list="hotList" />
          </van-panel>
          <van-panel title="热门视频" desc status>
            <vlist :mv-list="mvList" />
          </van-panel>
        </van-tab>
        <van-tab title="歌手">
          <Singer :singer-list="singerList" />
        </van-tab>
        <van-tab title="我的">
          <vlist :mv-list="mvList" />
        </van-tab>
      </van-tabs>
    </div>
  </keep-alive>
</template>

<script>
import List from "../components/List.vue";
import Tabb from "../components/Tabb.vue";
import Singer from "../components/Singer.vue";
import Vlist from "../components/Vlist.vue";
import { mapState, mapActions } from "vuex";
export default {
  name: "HelloWorld",
  data() {
    return {
      show: false,
      active: "home",
      picList: [],
      hotList: [],
      singerList: [],
      mvList: []
    };
  },
  components: {
    List,
    Tabb,
    Singer,
    Vlist
  },
  props: {
    msg: String
  },
  created() {
    this.init();
    this.getHotList();
    this.getSingerList();
    this.getVideoList();
  },
  computed: {
    ...mapState(["playHistory"])
  },
  methods: {
    ...mapActions(["getGeListActions"]),
    async init() {
      const this_ = this;
      this.$http
        .get(this.host+"/banner?type=2", { param: {} })
        .then(function(response) {
          this_.picList = response.data.banners.map(item => item.pic);
        })
        .catch(error => {
          console.log("接口或处理逻辑出错");
        });
    },
    async getHotList() {
      const this_ = this;
      this.$http
        .get(this.host+"/personalized", { params: { limit: 6 } })
        .then(function(response) {
          this_.hotList = response.data.result;
       
         
        })
        .catch(error => {
          console.log("接口或处理逻辑出错");
        });
    },
    async getSingerList() {
      const this_ = this;
      this.$http
        .get(this.host+"/artist/list", { params: { limit: 100, type: -1 } })
        .then(function(response) {
          this_.singerList = response.data.artists;
        })
        .catch(error => {
          console.log("接口或处理逻辑出错");
        });
    },
    async getVideoList() {
      const this_ = this;
      this.$http
        .get(this.host+"/mv/first", { params: { limit: 10, type: -1 } })
        .then(function(response) {
          this_.mvList = response.data.data;
        })
        .catch(error => {
          console.log("接口或处理逻辑出错");
        });
    },
    showPopup() {
      this.show = true;
    }
  }
};
</script>

 